.flipping-page {
  display: flex;

  &--width {
    @media (min-width: 768px) {
      width: 35%;
      justify-content: flex-end;
    }
  }
}

.flipping-page svg {
  width: 26px;
  height: 12px;

  @media (min-width: 768px) {
    width: 31px;
    height: 13px;
  }
}

.flipping-page__flip {
  text-decoration: none;
  transition: all 0.2s;

  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.4;
  }

  &--prev {
    margin-right: 30px;
  }
}
